   color: #fff;
   position: relative;
   margin-bottom: 10px;
 }
 
 .hs-title-1 span,
 .hs-title-2 span,
 .hs-title-3 span {
   display: inline-block;
   border-radius: 5px;
   position: relative;
   -webkit-box-shadow: -1px -5px 20px 0px rgba(0, 0, 0, 0.3);
           box-shadow: -1px -5px 20px 0px rgba(0, 0, 0, 0.3);
   opacity: 0;
 }
 
 .hs-title-1 {
   font-size: 72px;
   z-index: 3;
 }
 
 .hs-title-1 span {
   padding: 0 28px;
   background: #ff2a6b;
   -webkit-transform: rotate(4deg);
       -ms-transform: rotate(4deg);
           transform: rotate(4deg);
 }
 
 .hs-title-2 {
   font-size: 48px;
   z-index: 4;
 }
 
 .hs-title-2 span {
   padding: 5px 28px;
   background: #fdc856;
   -webkit-transform: rotate(0deg);
       -ms-transform: rotate(0deg);
           transform: rotate(0deg);
   left: 0;
   top: 0;
 }
 
 .hs-title-3 {
   font-size: 36px;
   z-index: 5;
 }
 
 .hs-title-3 span {
   padding: 10px 28px;
   background: #bdde64;
   -webkit-transform: rotate(0deg);
       -ms-transform: rotate(0deg);
           transform: rotate(0deg);
   top: 0;
 }
 
 .owl-item.active .hero-slide-item .hs-title-1 span,
 .owl-item.active .hero-slide-item .hs-title-2 span,
 .owl-item.active .hero-slide-item .hs-title-3 span {
   opacity: 1;
 }
 
 .owl-item.active .hero-slide-item .hs-title-1 span {
   -webkit-transform: rotate(0);
       -ms-transform: rotate(0);
           transform: rotate(0);
   -webkit-transition: all 0.4s ease 0.6s;
   -o-transition: all 0.4s ease 0.6s;
   transition: all 0.4s ease 0.6s;
 }
 
 .owl-item.active .hero-slide-item .hs-title-2 span {
   -webkit-transform: rotate(-6deg);
       -ms-transform: rotate(-6deg);
           transform: rotate(-6deg);
   left: 120px;
   top: -15px;
   -webkit-transition: all 0.4s ease 0.8s;
   -o-transition: all 0.4s ease 0.8s;
   transition: all 0.4s ease 0.8s;
 }
 
 .owl-item.active .hero-slide-item .hs-title-3 span {
   -webkit-transform: rotate(3deg);
       -ms-transform: rotate(3deg);
           transform: rotate(3deg);
   top: -15px;
   -webkit-transition: all 0.4s ease 1s;
   -o-transition: all 0.4s ease 1s;
   transition: all 0.4s ease 1s;
 }
 
 .hero-slider .owl-next {
   position: absolute;
   width: 40px;
